 
  .avatar{
    display: flex;
    border-radius: 50%;
    width: 70%;
    margin-bottom: 5px;
    margin-left: 11%;
    margin-top: 5%;
    border-style: solid;
    border-width: 2px;
    padding: 2px;
  }
  .name{
    /* text-align: center; */
    color: black;
    text-decoration: none;
  }
  .name:hover{
    text-decoration: underline;
    color: rgb(90, 90, 255)
  }
  .userContainer{
    width: 90px;
    margin-left: 15px;
    margin-bottom: 20px;
    float: left;
    text-decoration: none;
    border-radius: 10%;
    color: black;
    position: relative
  }
  .userContainer:hover{
    background-color: rgba(0, 0, 0, 10%);
  }
  .choosed{
    background-color: rgba(0, 0, 0, 20%);
  }
  .choosed:hover{
    background-color: rgba(0, 0, 0, 20%);
  }
  .lock{
      width: 30px;
      height: 30px;
      opacity: 0.7;
      position: absolute;
      right: 5px;
      bottom: 45px;
  }